.row{
	color: white;
	margin-left: 20px;
}

.row__posters{
	display: flex;
	overflow-y: hidden;
	overflow-x: scroll;
	padding: 20px;
}

.row__posters::-webkit-scrollbar {
	display: none;
}

.row__poster{
	max-width: 100px;
	object-fit: contain;
	margin-right: 10px;
	transition: transform 450ms;
}

.row__poster:hover {
	transform: scale(1.08);
	opacity: 1;
}

.row__posterLarge {
	max-height: 250px;

}

/* transform: scale(5.09) is to make image bigger on hover-over */
.row__posterLarge:hover {
	transform: scale(1.09);
	opacity: 1;
}

